'use strict';

/**
* Create an array containing pseudorandom numbers drawn from a Fréchet distribution.
*
* @module @stdlib/random-array-frechet
*
* @example
* import frechet from '@stdlib/random-array-frechet';
*
* var arr = frechet( 10, 2.0, 5.0, 3.0 );
* // returns <Float64Array>
*
* @example
* import frechet from '@stdlib/random-array-frechet';
*
* var arr = frechet( 10, 2.0, 5.0, 3.0, {
*     'dtype': 'generic'
* });
* // returns [...]
*
* @example
* import frechet from '@stdlib/random-array-frechet';
*
* var rand = frechet.factory( 2.0, 5.0, 3.0 );
* // returns <Function>
*
* var arr = rand( 10 );
* // returns <Float64Array>
*
* @example
* import frechet from '@stdlib/random-array-frechet';
*
* var rand = frechet.factory( 2.0, 5.0, 3.0 );
* // returns <Function>
*
* var arr = rand( 10, {
*     'dtype': 'generic'
* });
* // returns [...]
*/

// MODULES //

import setReadOnly from '@stdlib/utils-define-nonenumerable-read-only-property';
import main from './main.js';
import factory from './factory.js';


// MAIN //

setReadOnly( main, 'factory', factory );


// EXPORTS //

export default main;

// exports: { "factory": "main.factory", "assign": "main.assign" }
